<template>
  <div class="end-fight">
    <!-- pre -->
    <div v-if="state==='pre'" class="fight-pre-box">
      <div class="fight-pre-bg" :style="{ backgroundImage: 'url(' + lightBg + ')' }"></div>
      <img class="fight-pre-word" src="@/assets/images/ling/fight-word.png" alt="fight-word">
      <div v-if="whoWin==='no'" class="fight-pre-text">你決定「同時壓制雙方」，戰鬥中記得把勢力標記<span style="color: white;">「維持在中間」</span>。機會只有一次，你準備好了嗎？</div>
      <div v-if="whoWin==='man'" class="fight-pre-text">你決定「協助館長」，戰鬥中記得把勢力標記<span style="color: white;">「移向館長」</span>。機會只有一次，準備好就開始戰鬥吧！</div>
      <div v-if="whoWin==='gg'" class="fight-pre-text">你決定「協助吉古拉」，戰鬥中記得把勢力標記<span style="color: white;">「移向吉古拉」</span> 。機會只有一次，準備好就開始戰鬥吧！</div>
      <div class="fight-pre-btn-box">
        <img @click="goToFight" class="fight-pre-btn" src="@/assets/images/ling/enter-in.png" alt="enter-in">
      </div>
    </div>
    <!-- ing -->
    <div v-else-if="state==='ing'" class="fight-ing-box">
      <div class="fight-ing-bg" :style="{ backgroundImage: 'url(' + lightBg + ')' }"></div>
      <img style="left: 0vw;" class="fight-ing-ling-icon" src="@/assets/images/ending/ending-gg-big.png" alt="fight-word">
      <img style="right: 0vw;" class="fight-ing-ling-icon" src="@/assets/images/ending/ending-man-big.png" alt="fight-word">
      <div class="fight-ing-time">{{ time }}</div>
      <div v-if="whoWin==='no'" class="fight-ing-bar" :style="{ backgroundImage: 'url(' + ballBar + ')' }">
        <img
          :style="{ marginLeft:  ballPosition + 'px' }"
          class="fight-ing-ball"
          src="@/assets/images/ling/play-ball.png"
          alt="play-ball">
      </div>
      <div v-if="whoWin==='man'" class="fight-ing-bar" :style="{ backgroundImage: 'url(' + rightBar + ')' }">
        <img
          :style="{ marginLeft:  ballPosition + 'px' }"
          class="fight-ing-ball"
          src="@/assets/images/ling/play-ball.png"
          alt="play-ball">
      </div>
      <div v-if="whoWin==='gg'" class="fight-ing-bar" :style="{ backgroundImage: 'url(' + leftBar + ')' }">
        <img
          :style="{ marginLeft:  ballPosition + 'px' }"
          class="fight-ing-ball"
          src="@/assets/images/ling/play-ball.png"
          alt="play-ball">
      </div>
      <div class="fight-ing-btn-box">
        <img @click="goLeft" class="fight-ing-btn" src="@/assets/images/ling/go-left.png" alt="go-left">
        <img @click="goRight" class="fight-ing-btn" src="@/assets/images/ling/go-right.png" alt="go-right">
      </div>
    </div>
    <!-- lose -->
    <div @click="goToPre" v-else-if="state==='lose'" class="fight-lose-box">
      <div class="fight-lose-bg" :style="{ backgroundImage: 'url(' + lightBg + ')' }"></div>
      <img class="fight-lose-ling-icon" src="@/assets/images/river/river-green-icon.png" alt="fight-word">
      <div class="fight-lose-ch">殘念</div>
       <div class="fight-lose-text">請重新補充能力值再戰</div>
    </div>
    <!-- win -->
    <div @click="goToCollect" v-else-if="state==='win'" class="fight-lose-box">
      <div class="fight-lose-bg" :style="{ backgroundImage: 'url(' + lightBg + ')' }"></div>
        <img class="fight-lose-ling-icon" src="@/assets/images/river/river-grey-icon.png" alt="fight-word">
        <div style="font-size: 18px; text-align: left;" class="fight-lose-ch">啊......我、我竟然輸了<br>嗚嗚嗚，我果然是最沒用的怪奇<br>我的存在一點利用價值都沒有......</div>
      </div>
  </div>
</template>

<script>

import lightBg from '@/assets/images/ending/ending-lighting.png'
import ballBar from '@/assets/images/ling/vertical-bar.png'
import leftBar from '@/assets/images/ling/fight-bar-left.png'
import rightBar from '@/assets/images/ling/fight-bar-right.png'
export default {
  name: 'EndFightTwo',
  components: {

  },
  data () {
    return {
      lightBg: lightBg,
      ballBar: ballBar,
      leftBar: leftBar,
      rightBar: rightBar,
      state: 'pre',
      time: 15,
      ballPosition: 0
    }
  },
  mounted () {

  },
  methods: {
    backToCompass () {
      this.$store.commit('changeHeader', true)
      this.$router.push({
        path: './monster-compass'
      })
    },
    goToFight () {
      this.state = 'ing'
      this.gameStart()
    },
    gameStart () {
      // 時間開始
      let round1 = setInterval(() => {
        this.time--
      }, 1000)

      setTimeout(() => {
        if (this.ballPosition < -160) {
          this.ballPosition = -250
        } else {
          this.ballPosition = this.ballPosition - 90
        }
      }, 2000)

      setTimeout(() => {
        if (this.ballPosition > 100) {
          this.ballPosition = 250
        } else {
          this.ballPosition = this.ballPosition + 150
        }
      }, 5000)

      setTimeout(() => {
        if (this.ballPosition > 60) {
          this.ballPosition = 250
        } else {
          this.ballPosition = this.ballPosition + 190
        }
      }, 9000)

      setTimeout(() => {
        if (this.ballPosition < -150) {
          this.ballPosition = -250
        } else {
          this.ballPosition = this.ballPosition - 100
        }
      }, 11000)

      setTimeout(() => {
        if (this.whoWin === 'man') {
          this.ballPosition = 250
        } else if (this.whoWin === 'gg') {
          this.ballPosition = -250
        } else {
          this.ballPosition = 0
        }
      }, 14800)

      // 時間結束
      setTimeout(() => {
        clearInterval(round1)
        if (this.whoWin === 'man') {
          alert('館長獲勝！')
        } else if (this.whoWin === 'gg') {
          alert('吉古拉獲勝！')
        } else if (this.whoWin === 'no') {
          alert('平手！')
        }
        this.$router.push({
          path: './end-talk-two'
        })
      }, 15000)
    },
    winGame () {
      alert('你贏了！')
      // this.$store.commit('changeHeader', true)
      // this.$store.commit('changeNoteDot', true)
      // this.$store.commit('changeFrameDot', true)
      // this.$store.commit('luluWin')
      this.state = 'win'
      // this.$store.commit('getLuluCard', 'grey')
    },
    goLeft () {
      console.log(this.ballPosition)
      if (this.ballPosition > 250) {
        this.ballPosition = 250
        return
      } else if (this.ballPosition < -250) {
        this.ballPosition = -250
        return
      }
      this.ballPosition = this.ballPosition - 10
    },
    goRight () {
      if (this.ballPosition > 250) {
        this.ballPosition = 250
        return
      } else if (this.ballPosition < -250) {
        this.ballPosition = -250
        return
      }
      this.ballPosition = this.ballPosition + 10
    },
    goToPre () {
      this.state = 'pre'
    },
    goToCollect () {
      this.$router.push({
        path: './river-house-end'
      })
    }
  },
  computed: {
    power () {
      return this.$store.state.power
    },
    whoWin () {
      return this.$store.state.whoWin
    }
  }
}
</script>
<style lang="scss" scoped>

  .end-fight {
    min-height: 100vh;
    background: linear-gradient(#000 70%, #95ff00);
    touch-action: manipulation;

    .fight-pre {

      &-box {
        min-height: 100vh;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      &-bg {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        z-index: 0;
      }

      &-ability {
        position: absolute;
        top: 15px;
        right: 20px;
        padding: 4px;
        color: black;
        background-color: #95ff00;
        border-radius: 4px;
        text-align: center;
        z-index: 1;
      }

      &-word {
        width: 75px;
        height: 100%;
        z-index: 1;
      }

      &-text {
        color: #95ff00;
        font-size: 16px;
        margin-top: 16px;
        text-align: center;
        padding: 0px 12px;
        z-index: 1;
      }

      &-btn-box {
        margin-top: 16px;
        display: flex;
        justify-content: space-around;
        z-index: 1;
      }

      &-btn {
        width: 80px;
        height: 80px;
        margin: 0px 20px;
      }
    }

    .fight-ing {

      &-box {
        min-height: 100vh;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      &-bg {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        z-index: 0;
      }

      &-ling-icon {
        position: absolute;
        top: 40px;
        width: 50vw;
        height: auto;
      }

      &-time {
        position: absolute;
        top: 20px;
        left: 20px;
        text-align: center;
        font-size: 30px;
        color: #95ff00;
        z-index: 2;
      }

      &-bar {
        margin-top: 160px;
        display: flex;
        justify-content: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        width: 300px;
        height: 50px;
        border-radius: 40px;
        z-index: 2;
      }

      &-ball {
        width: 50px;
        height: 50px;
      }

      &-btn-box {
        margin-top: 16px;
        display: flex;
        justify-content: space-around;
        width: 300px;
        z-index: 1;
      }

      &-btn {
        width: 80px;
        height: 80px;
      }

    }

    .fight-lose {

      &-box {
        min-height: 100vh;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      &-bg {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        z-index: 0;
      }

      &-ling-icon {
        width: 140px;
        height: auto;
        z-index: 2;
      }

      &-ch {
        text-align: center;
        font-size: 40px;
        color: #95ff00;
        z-index: 2;
      }

      &-text {
        color: #95ff00;
        text-align: center;
        z-index: 2;
      }
    }
  }

</style>



